.box {

  background: #444;
  border-radius: 3px;
  cursor: pointer;
  border: 1px solid #444;
  display: flex;
  justify-content: center;

  overflow: hidden;
  width: 15rem;
  height: 20rem;

  border-radius: 3px;
  overflow: hidden;

  @media only screen and (max-width: 900px) {
    width: 10rem;
    height: 15rem;
  }

  @media only screen and (max-width: 600px) {
    width: 7rem;
    height: 10rem;
  }
  &:hover img {
    transform: scale(1.2);
    opacity: .9;
  }
  p {
    user-select: none;
    font-weight: 500;
    color: black;

  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    transition: .3s all ease;


  }
}